/* Основные стили */

/* Слайдер */
.slick-slider{
	min-width: 0;
}
/* Ограничивающая оболочка */
.slick-list {
	overflow: hidden;
}
/* Лента слайдов */
.slick-track{
	display: flex;
	align-items: flex-start;
}

.slider {
	position: relative;
	/* padding-left:250px; */
    left: 125px;
}

@media (max-width:767px){
	.slider {
		padding: 0px 40px;
	}
}

.slider .slick-arrow{
	position: absolute;
    margin:  0px 0px 0px -100px;
	bottom: 60px;
	z-index: 10;
	font-size: 0;
	width: 60px;
	height: 50px;
   
}
.slider .slick-arrow.slick-prev{
	left: -61px;
	background: url('../img/arrow-2.svg') 0 0 / 100% no-repeat;
}
.slider .slick-arrow.slick-next{
	left: -120px;
	background: url('../img/arrow-1.svg') 0 0 / 100% no-repeat;
}

